.download-page h2 {
  font-weight: bold;
  font-size: 27px;
  line-height: 32px;
  letter-spacing: 0.01em;
  color: #000624;
  color: #ffffff;
  margin-bottom: 8px;
  text-align: center;
  width: 100%;
}
.download-page p.subtitle {
  font-size: 16px;
  line-height: 24px;
  color: #737a91;
  max-width: 671px;
  margin-bottom: 36px;
  color: #ffffff;
  text-align: center;
  max-width: 590px;
  margin: auto;
  margin-bottom: 40px;
}
.download-page header {
  background: url("../images/BG.png"), no-repeat;
  background-size: cover;
  height: 350px;
  width: 100%;
}
.download-page .page {
  max-width: 1150px;
  margin: auto;
  margin-top: -130px;
  margin-bottom: 88px;
}
.download-page .download {
  background: #f3f3f8;
  border-radius: 4px;
  padding: 24px;
  max-width: 724px;
  width: 100%;
  margin: auto;
}
.download-page .download--title {
  font-weight: 600;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #737a91;
  margin-bottom: 16px;
}
.download-page .download .code {
  background: #000624;
  border-radius: 4px;
  padding: 24px;
  width: 100%;
  color: #ffffff;
  flex-flow: column nowrap;
  font-size: 13px;
  line-height: 20px;
}
.download-page .download .code span {
  color: #477db3;
}
.download-page .download .code--flex {
  display: flex;
}
.download-page .download .code--flex span {
  margin-right: 4px;
}
.download-page .download .code--flex-code {
  display: flex;
  flex-flow: column;
}
.download-page .download--view-more {
  display: flex;
  justify-content: flex-end;
  margin-top: 27px;
}
.download-page .download--view-more--links {
  display: flex;
  align-items: center;
}
.download-page .download--view-more.flex-between {
  justify-content: space-between;
}
.download-page .download--view-more a {
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  color: #477db3;
  white-space: nowrap;
}
.download-page .download--view-more a img {
  width: 16px;
  height: 16px;
  margin-left: 8px;
}
.download-page .download--view-more a:not(:last-of-type) {
  margin-right: 32px;
}
.download-page .download--view-more a.underlined {
  text-decoration: underline;
}